

body {
  margin: 0;
  font-family: 'Inter', sans-serif;
}

p {
  color: #2B283A;
  font-size: .7rem;
  line-height: 150%;
  margin-top: -.9rem;
  }

hr {
  background-color: #F5F5F5;
  width: 22rem;
height: 0.0625rem;
display: block;
}

a {
color: #918E9B;
font-family: Inter;
font-size: 0.64rem;
font-style: normal;
font-weight: 400;
line-height: normal;
text-decoration-line: underline;
margin-left: .5rem;
}

* {
  box-sizing: border-box;
}

.header {
  background-color: #F55A5A;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3.5rem;
  margin-bottom: 2rem;
}

.header p {
  margin-left: .5rem;
  color: #FFF;
  font-size: 1rem;
  margin-top: 1rem;

}

.card {
  display: flex;
  margin: .5rem 2rem;
}

.location-box {
  margin: .5rem 1rem;
}

h2 {
  margin-top: -1px;
}

.map {
  display: flex;
  align-items: center;
  margin-bottom: .3rem;
}

p.text-info {
  width: 20rem;
}
.card-container {
  display: flex;
  flex-wrap: wrap;
}

.location-img {
  width: 7.8rem;
  height: 10.5rem;
  border-radius: 0.3rem;

}

span {
color: #2B283A;
font-family: Inter;
font-size: 0.64rem;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.10881rem;
margin-left: .2rem;
text-transform: uppercase;
}
